/* NAVBAR */

.navbar {
  background-color: #4C6A6E;
}

.navbar-item {
  color: white !important
}

.custom-btn {
  font-family: 'Raleway', sans-serif !important;
  border: none !important;
  color: white !important;
  background-color: rgba(0,0,0,0) !important;
}

.itemStyle {
  color: rgba(255,255,255,.8) !important
}

.whiteBar {
  background-color: white;
  height: 10px;
}

#basic-nav-dropdown {
  color: rgba(255,255,255,.8) !important
}

/* FOOTER */
.footer{
  background-color: rgb(51,51,51);
  padding-top: 20px;
  padding-left: 20% !important;
  padding-right: 20% !important;
  max-width: 100% !important;
}
.footerItems {
  text-align: center;
  color: white;
  font-family: 'Raleway', sans-serif;
  padding-bottom: 35px;
}

.footerText {
  font-size: 14px;
}

.subscribeBox {
  background-color: rgb(85,85,85);
  max-width: 100%;
  padding: 15px !important;
}

.subscribeButton {
  background-color: rgb(200,200,200) !important;
  color:black !important;
  border:none !important;
  border-radius: 0% !important;
  text-align: center !important;
  font-family: 'Raleway', sans-serif;
}

.subscribeButton:hover {
  background-color: rgb(250,250,250) !important;
}

.emailBox {
  background-color: rgb(130,130,130) !important;
  color: white !important;
  border: none !important;
  max-width: 100%;
  border-radius: 0% !important;
}

.form-control::-webkit-input-placeholder {
  color: white !important;
}

/* HOME */
.featBox {
  background-color: rgba(81,81,81,.5);
  padding: 3vw 0;
  margin: auto;
  text-align: center;
}

.featText {
  font-family: 'Spinnaker', sans-serif;
  color: white;
  font-weight: 700;
  letter-spacing: 0.92px;
  font-size: 46px;
}

.featButton {
  letter-spacing: 0.5px;
  background-color: rgb(85,85,85) !important;
  color:white !important;
  border-radius: 0px !important;
  border:none !important;
  align-items: center;
  font-size: 18px !important;
}

.featButton:hover {
  background-color: rgb(250,250,250) !important;
  color: rgb(85,85,85) !important;
}
.newReleaseDiv {
  background-color: rgb(102,102,102);
  color: white;
  height: 3rem;
  text-align: center;
  letter-spacing: 3.9px;
  padding: 10px;
  font-weight: 500;
}

.newReleaseText {
  font-family: 'Spinnaker', sans-serif;
  font-size: 36px;
  font-weight: 700;
  letter-spacing: 10.8px;
  color: rgb(85,85,85);
}

.newReleaseText2 {
  font-family:'Raleway',sans-serif; 
  font-size:19px; 
  letter-spacing:0.3em; 
  text-align:center
}

.cardBtn {
  background-color: rgb(51,51,51) !important;
  border-radius: 0px !important;
  border: none !important;
  letter-spacing: 0.8px;
  color: white !important;
  width: 100%;
  font-weight: 600;
}

.cardBtn:hover {
  background-color: rgb(90,90,90) !important;
}

/* ABOUT US */

.aboutUsText {
  font-family: 'Raleway', sans-serif;
  font-size: 19px;
  color: black;
  color: rgb(85,85,85);
}

.mvTitle {
  font-family: 'Spinnaker', sans-serif;
  font-size: 47px;
  font-weight: 700;
  color: black;
  text-align: center;
}

.mvText {
  color: rgb(85,85,85);
  font-family: 'Raleway', sans-serif;
  font-size: 29px;
  text-align: center;
}

/* CONTACT US*/

.contactUsText {
  font-family: 'Raleway', sans-serif;
  font-size: 22px;
  color: rgb(85,85,85);
  text-align: center;
}

.formText {
  font-family: 'Raleway', sans-serif;
  text-align: center;
  font-size: 22px;
}

.formBox {
  background-color: rgb(85,85,85);
  font-family: 'Raleway', sans-serif;
  font-size: 18px;
  padding: 10px;
}

.formInput {
  background-color: rgba(130,130,130,.5) !important;
  color: white !important;
  border: none !important;
  max-width: 100%;
  border-radius: 0% !important;
}

.formSubmit {
  letter-spacing: 0.5px;
  background-color: rgb(51,51,51) !important;
  color:white !important;
  border-radius: 0px !important;
  border:none !important;
}

.formSubmit:hover {
  background-color: rgba(255,255,255) !important;
  color: rgb(85,85,85) !important;
}

.followText {
  font-family: 'Raleway', sans-serif;
  font-size: 22px;
  font-weight: 700;
  text-align: center;
  letter-spacing: 5.7px;
  color: rgb(85,85,85);
}


.socialMediaIcons {
  color: black;

}

.map-container {
  height: 500px;
}


/* MULTIPURPOSE */

.parentHeight {
  height: 100%;
}

.bgBlue {
  background-color: rgba(185, 215, 211, 0.4);
  margin-bottom: 10px;
}

.pageTitle {
  font-family: 'Spinnaker', sans-serif;
  font-size: 33px;
  font-weight: 700;
  text-align: center;
}



